export default {
  async asyncData({ $content, params, error }) {
    try {
      const [project] = await $content(params.theme, params.slug)
        .where({ slug: params.project })
        .only(["title", "img", "alt"])
        .fetch();

      return { project };
    } catch (err) {
      error({
        statusCode: 404,
        message: "Project could not be found",
      });
    }
  },
};
